import React, { useEffect, useState } from "react";
import { useHistory, Link } from "react-router-dom";

const AddList = () => {
  const [box, setbox] = useState("");
  const [listItem, setlistItem] = useState("");
  const [status, setstatus] = useState("Add items to the list");
  const [listItemArray, setlistItemArray] = useState([]);
  const [warning, setwarning] = useState("");
  const cdiv = { "margin-top": "70px" };
  const j = {
    cursor: "pointer",
    "text-decoration": "none",
    color: "black",
    "font-weight": "bold",
  };
  //Lifecycles

  useEffect(() => {
    getTodolist();
    getTodolistArray();
  }, []);
  useEffect(() => {
    saveList();
  }, [listItem, listItemArray]);

  const history = useHistory();

  //localstorage
  const saveList = () => {
    localStorage.setItem("list", JSON.stringify(listItem));
    localStorage.setItem("listA", JSON.stringify(listItemArray));
  };

  const getTodolist = () => {
    if (localStorage.getItem("list") === null) {
      localStorage.setItem("list", JSON.stringify([]));
    } else {
      const list = JSON.parse(localStorage.getItem("list"));
      setlistItem(list);
    }
  };
  const getTodolistArray = () => {
    if (localStorage.getItem("listA") === null) {
      localStorage.setItem("listA", JSON.stringify([]));
    } else {
      const listA = JSON.parse(localStorage.getItem("listA"));
      setlistItemArray(listA);
    }
  };
  //Functions
  const timer = () => {
    setTimeout(() => {
      setstatus("Add items to the list");
      setwarning("");
    }, 2000);
  };

  const itemChoose = (key) => () => {
    setlistItem(listItemArray[key]);
    setTimeout(() => {
      history.push("/class");
    }, 10);
  };
  const inputHandler = (event) => {
    event.target.name = setbox(event.target.value);
  };

  const addItem = () => {
    if (box === "") {
      setstatus("*Enter something to add");
      setwarning("warning");
      timer();
    } else {
      setlistItemArray([...listItemArray, box]);
      setbox("");
    }
  };

  const itemDelete = (key) => () => {
    setlistItemArray(listItemArray.filter((item, index) => index !== key));
    setstatus("*" + listItemArray[key] + " Deleted");
    setwarning("warning");
    timer();
  };

  const enterkey = (e) => {
    e.preventDefault();
  };

  return (
    <div>
      <div className="fdiv">
        <div style={cdiv} className="cdiv">
          <h1>Add List</h1>
          <p className={warning}>{status}</p>
          <form action="" onSubmit={enterkey}>
            <input type="text" value={box} name="box" onChange={inputHandler} />
            <i onClick={addItem} class="far fa-calendar-plus kp"></i>
            <ul>
              {listItemArray.map((data, key) => {
                return (
                  <div className="additem">
                    <li
                      style={{ width: "90%", "margin-right": "0px" }}
                      key={key}
                      onClick={itemChoose(key)}
                    >
                      {data}
                    </li>
                    <li>
                      <i
                        onClick={itemDelete(key)}
                        class="fas fa-trash-alt  testdiv2"
                      ></i>
                    </li>
                  </div>
                );
              })}
            </ul>
          </form>
          <p>Before deleting please clear the list first</p>
          <Link style={j} to="/">
            Home &nbsp;&nbsp;<i class="fas fa-arrow-left"></i>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default AddList;
