import React from "react";
import { useHistory } from "react-router-dom";
export default function Deleteprompt() {
  const i = { "margin-top": "70px" };
  const cdiv = { "margin-top": "70px" };
  const history = useHistory();
  const onYes = () => {
    localStorage.clear();
    history.push("/");
  };
  const onNo = () => {
    history.push("/");
  };
  return (
    <div>
      <div className="fdiv">
        <div style={cdiv} className="cdiv">
          <h1>
            <i class="fas fa-radiation fa-spin"></i>
          </h1>
          <h1>Do you want to clear?</h1>
          <p className="warning">
            This will clear all your data <i class="far fa-frown"></i>
          </p>
          <h3 style={i} onClick={onYes}>
            Yes <i class="far fa-thumbs-up"></i>
          </h3>
          <h3 onClick={onNo}>
            No <i class="fas fa-exclamation"></i>
          </h3>
        </div>
      </div>
    </div>
  );
}
